.App {
  text-align: center;
  background-color: #e9ecef
}
.root{
  background-color: #e9ecef;
}

.countryContainer {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 15px;
  background-color: #e9ecef;
  padding-top: 30px;
}

.jumbotron {
  background-image: url("./map-of-the-world-2401458_1920.jpg");
  background-position: cover;
  width: 100%;
  height: 800px;
}


.homeTitle {
  font-weight: bold;
}

.homeText {
  font-weight: bold;
  font-size: 20px;
}

.countryForm{
 padding: 50px 300px 0px 300px;
}

 .flag{
width:286px;
height: 166px;
background-size: cover;
background-position: center;
}

.asylum{
  border: 1px solid rgba(0,0,0,.125);
  border-radius: 5px;
}

.asylumInfo{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 10px;
}

